import { Location } from '@angular/common';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Component, OnInit, Input, Output, EventEmitter, AfterViewInit, ViewChild } from '@angular/core';

import { Message } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { checkCard } from 'app/@uisftech/common/service/validator';
import { CreditBorrowerBean } from '../../bean/credit-borrower.bean';
import { CreditDebt } from 'app/pages/tzb/custom/credit-ccm/credit-debt/credit-debt.component';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { CreditDebtBean } from '../../credit-guarantee/bean/guarantor.bean';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';

@Component({
    selector: 'app-credit-borrower-add',
    templateUrl: './credit-borrower-add.component.html',
    styleUrls: ['./credit-borrower-add.component.scss'],
    providers: [CreditCcmService]
})

// 
// tslint:disable-next-line:component-class-suffix
export class CreditBorrowerAdd implements OnInit {
    debtValue: { transferCreditData: { custName: any; custNo: any; flag: string; orgCode: any; }; };
    idenNum: any;
    custNames: any;
    custNos: any;
    creditDebtFlag: boolean = false;
    code: any;
    // facProcessQuestionId
    @Input() param_data: any;

    // 
    @Output() closeDialog = new EventEmitter();

    // 
    borrowerRelationshipList = [];

    // 
    msgs: Message[] = [];
    cert_type=[];
    commonBorrowerCustName: string;
    custName: string;
    customerListBean={};
    pageSizes=10;
    pageNum=1;
    first=0;
    // 
    successMsgs: Message[] = [];
    dataTable=[];
    totalNum:any;
    display2=false;
    // 
    cqiId = '';
    // isShowTip: boolean = true;
    custNo: string;
    // bean
    creditBorrowerBean: CreditBorrowerBean = new CreditBorrowerBean();
    creditDebtBean: CreditDebtBean = new CreditDebtBean();
    // 
    myformChild: FormGroup = this.fb.group({
        'commonBorrowerCustNo': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9]{1,15}$/), Validators.required]),
        'custName': new FormControl('', [Validators.required]),
        'commonBorrowerCustName': new FormControl('', [Validators.pattern(/^[a-zA-Z0-9\u4e00-\u9fa5]{1,30}$/), Validators.required]),
        'commonBorrowerCardNO': new FormControl('', [Validators.required]),
        'commonBorrowerRemark': new FormControl(),
        'commonBorrowerRelation': new FormControl('', [Validators.required]),
        // 'commonBorrowerTelephone': new FormControl(),
        // 'commonBorrowerCardType': new FormControl()
    }); //

    @ViewChild(CreditDebt)
    private creditDebt: CreditDebt;
    isHasCoBorrower: any;//
    coBorrowerType:any;// 
    coBorrowerTypeName:any;// 
    // 
    constructor(
        public fb: FormBuilder,
        public commfunc: Commfunc,
        private creditCcmService: CreditCcmService,
        private communicationService: ParentChildCommunicationService,
        private location: Location
    ) {
        this.getCodeValues();
        // 
        this.borrowerRelationshipList = this.code['borrowerRelationship'];
        this.cert_type=this.code['CERT_TYPE'];
    }

    // 
    ngOnInit() {
        this.creditBorrowerBean.custName = this.param_data.custName;
        this.custNo = this.param_data.custNo;
        this.isHasCoBorrower = this.param_data.isHasCoBorrower;
        this.coBorrowerType = this.param_data.coBorrowerType;
        this.coBorrowerTypeName = this.param_data.coBorrowerTypeName;
     
        //  
        if (this.param_data.type === 'update') {
            this.queryFacQuestions(this.param_data);
            this.cqiId = this.param_data.detailBorrower.cqiId;
        }
    }

    // 
    getCodeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret // code

    }
    choiceRate(){
        this.display2=true;
    }
    querySecle() {
        this.first = 0;
        this.queryList();
      }
      queryList() {
        this.customerListBean['pageSize'] = this.pageSizes;
        this.customerListBean['pageNum'] = this.pageNum;
        // this.doClearCheck()
        let ob = this.creditCcmService.myCustDimQry(this.customerListBean);
        ob.subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.dataTable = data.infoList;
            this.totalNum = data.totalNum;
        }
          else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
      }
      paginate(event) {
        //
        this.pageSizes = event.rows * 1;
        //
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = event.page * event.rows;
        //
        this.queryList();
      }
      choiceRates(col){

        let param = {'custNo':col.custNo};
        this.creditCcmService.selectCustBriefInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {                
                if(this.isHasCoBorrower){
                    if(this.coBorrowerType){
                        if(data.custType){
                            if((this.coBorrowerType == "1" && data.custType=="2") || (this.coBorrowerType == "2" && data.custType=="1")){
                                this.custNos=col.custNo;
                                this.custNames=col.custName;
                                this.idenNum=col.idenNum;
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'info', summary: '', detail: ""+this.coBorrowerTypeName+"" });
                                return;
                            }
                        }                        
                    } else {
                        this.custNos=col.custNo;
                        this.custNames=col.custName;
                        this.idenNum=col.idenNum;
                    }
                } else {
                    this.custNos=col.custNo;
                    this.custNames=col.custName;
                    this.idenNum=col.idenNum;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })        
        
      }
      goSecle(){
        this.creditBorrowerBean.commonBorrowerCustNo=this.custNos;
        this.creditBorrowerBean.commonBorrowerCustName=this.custNames;
        this.creditBorrowerBean.commonBorrowerCardNO = this.idenNum;
        this.creditBorrowerBean.commonBorrowerRelation = '';
        this.queryCusts();
        this.display2=false;
      }
      cancalSecle(){
        this.display2=false;
      }
    // 
    queryCusts() {
        // this.isShowTip = false;
        const commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let headerDataSourceId = '';
        if ((commonHeader.targetId).endsWith('0')) {
            headerDataSourceId = 'BR00A';
        } else if ((commonHeader.targetId).endsWith('1')) {
            headerDataSourceId = 'BR00B';
        } else {
            this.msgs = [{ severity: 'error', summary: '', detail: commonHeader.targetId + '01' }];
        }
        const params = {
            'custNo': this.creditBorrowerBean.commonBorrowerCustNo
        };
        //  
        this.creditCcmService.queryCustNeedInfo(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                // check:
                if (data.dataSourceId !== headerDataSourceId) {
                    this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                    return;
                }
                // 
                setTimeout(() => {
                    this.queryEsbDebtInfo(this.creditBorrowerBean.commonBorrowerCustNo, this.creditBorrowerBean.commonBorrowerCustName)
                }, 100);

            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        });

    }

    // 
    addBorrower() {
        // this.isShowTip = true;

        // 
        for (const i in this.myformChild.controls) {
            this.myformChild.controls[i].markAsDirty();
        }
        //
        for (const i in this.myformChild.controls) {
            if (!this.myformChild.controls[i].valid) {
                return false;
            }
        }
        // check 1:;
        if (this.creditBorrowerBean.commonBorrowerRelation === '01' && this.custNo != this.creditBorrowerBean.commonBorrowerCustNo) {
            this.msgs = [{ severity: 'error', summary: '', detail: ',' }];
            return;
        }
        //  check 2:;
        if (this.creditBorrowerBean.commonBorrowerRelation != '01' && this.creditBorrowerBean.custName === this.creditBorrowerBean.commonBorrowerCustName) {
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return;
        }

        const params = {
            'custRequestNo': this.param_data['custRequestNo'],
            'questionType': '01',
            'questionReslponse': JSON.stringify(this.creditBorrowerBean),
        };
        if (this.cqiId !== '') {
            params['cqiId'] = this.cqiId;
        }
        this.creditCcmService.saveFacQuestion(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.successMsgs = [{ severity: 'success', summary: '', detail: '' }];
            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        });

    }


    // 
    queryFacQuestions(facQuestions) {
        const params = {
            'custRequestNo': this.param_data['custRequestNo'],
            'questionType': '01',
            'cqiId': facQuestions.detailBorrower.cqiId
        };
        this.creditCcmService.queryFacQuestions(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                for (const item of data.list) {
                    const questionReslponse = JSON.parse(item['questionReslponse']);
                    questionReslponse.cqiId = item['cqiId'];
                    this.creditBorrowerBean = questionReslponse;
                    setTimeout(() => {
                        this.queryEsbDebtInfo(this.creditBorrowerBean.commonBorrowerCustNo, this.creditBorrowerBean.commonBorrowerCustName)
                    }, 100);
                }
            } else {
                this.msgs = [{ severity: 'error', summary: ':', detail: data.returnCode.message }];
            }
        });
    }

    // 
    toClose() {
        this.closeDialog.emit();
    }


    // 
    queryEsbDebtInfo(cust, custName) {
        let session = window.sessionStorage;
        let orgId = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
        let param = {
            custName: custName,
            custNo: cust,
            flag: '1',
            orgCode: orgId
        };
        this.debtValue={
            transferCreditData:param,
         }
    }
}