    creditDebtBeanShow: boolean = false;//
    age_this: any;

    ordinaryReadonly: boolean = false; //  Readonly
    pledgeAmtReadonly: boolean = false; //  Readonly
    startDateReadonly: boolean = false; //   Readonly
    endDateReadonly: boolean = false; //  Readonly

    getApplyAmountFromCredit: string; // 

    globalSeqNo: string;//
