    /**
     * 
     * : 1.,   2. 
     * : 1., 2. 3.
     */
    contractModeChange(item) {
        if(item=='03'){
            this.iszge=true;
            this.endDateReadonly=false;
            this.guaranteeContractAmtFlag = true;
        }else{
            this.endDateReadonly=true;
            this.iszge=false;
            this.guaranteeContractAmtFlag = false;
            this.mortgageBean.contractAmt='';
            this.myform.removeControl('contractAmt');
            this.myform.addControl('contractAmt', this.fb.control(''));
        }
        if (this.contractPatterns == '03') {
            if (item === '03') {
                this.startDateReadonly = false;
                // -
                this.ContractModeFlag = true;
                this.myform.addControl('maxContractType', this.fb.control('', [Validators.required]));
                this.guaranteeContractAmtFlag = true;
                this.myform.addControl('contractAmt', this.fb.control('', [Validators.required]));
            } else if (item === '01') {
                this.startDateReadonly = true;
                // -
                this.myform.removeControl('maxContractType');
                this.myform.addControl('contractAmt', this.fb.control(''));
                
                this.ContractModeFlag = false;
                this.guaranteeContractAmtFlag = false;                
            } else {
                this.mortgageBean.contractMode = '';
                this.myform.addControl('contractAmt', this.fb.control(''));
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                return;
            }
        } else if (this.contractPatterns == '04') {
            if (item === '01') {
                this.startDateReadonly = true;
                this.endDateReadonly = true;
                // -
                this.myform.removeControl('maxContractType');
                this.myform.addControl('contractAmt', this.fb.control(''));
                
                this.ContractModeFlag = false;
            } else if (item === '04') {//
                this.startDateReadonly = false;
                this.endDateReadonly = false;
                this.myform.addControl('contractAmt', this.fb.control(''));
            } else {
                this.mortgageBean.contractMode = '';
                this.myform.removeControl('contractMode');
                this.myform.addControl('contractAmt', this.fb.control(''));
                
                this.myform.addControl('contractMode', this.fb.control('', [Validators.required]));
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                return;
            }
        } else if (this.contractPatterns == '01') {
            if (item === '01') {
                this.startDateReadonly = true;
                this.endDateReadonly = true;
                // -
                this.myform.removeControl('maxContractType');
                this.ContractModeFlag = false;
                this.myform.addControl('contractAmt', this.fb.control(''));
                
                this.guaranteeContractAmtFlag = false;
            }
        }
    }
