    // 
    addCreditGuarantor(tab) {
        if (!this.preValidateForSubmit()) return;
        // check ()
        if (Number(this.mortgageBean.pledgeAmt) > Number(this.mortgageBean.pledgeContractBalance)) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return;
        } 
        // check 
        if ((this.inValue['creditCardElement'] && this.inValue['creditCardElement'] === 'creditCardElementSendCustRequestNo') ||
            (this.inputValueData && this.inputValueData.parentData && this.inputValueData.parentData.varMap && this.inputValueData.parentData.varMap.taskCategoryId === 'RUM0009')) {
            if (this.mortgageBean.contractMode != '01') { // ''
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                return
            }
        }
        if (this.mortgageBean.pledgeAmt) {//
            this.pledgeAmtChange('1');    
        }
        if(this.mortgageBean.contractAmt){//
            this.pledgeAmtChange('2');           
        }
        
        this.doChangeAmt('del', 'guaranteeContractAmt');//
        this.doChangeAmt('del', 'pledgeAmt');//
        
        this.mortgageBean.useAmt = this.mortgageBean.pledgeAmt;        

        // this.mortgageBean.guaranteeMode = '';
        if (!this.mortgageBean.guaranteeStartDate.length) {
            this.mortgageBean.guaranteeStartDate = this.commfunc.transDateN(this.mortgageBean.guaranteeStartDate);
        } else {
            this.mortgageBean.guaranteeStartDate = this.mortgageBean.guaranteeStartDate;
        }
        if (!this.mortgageBean.guaranteeEndDate.length) {
            this.mortgageBean.guaranteeEndDate = this.commfunc.transDateN(this.mortgageBean.guaranteeEndDate);
        } else {
            this.mortgageBean.guaranteeEndDate = this.mortgageBean.guaranteeEndDate;
        }
        this.mortgageBean.registerStartDate = this.commfunc.transDateN(this.mortgageBean.registerStartDate);
        this.mortgageBean.registerEndDate = this.commfunc.transDateN(this.mortgageBean.registerEndDate);

        this.outputValue.emit(this.mortgageBean);
        this.blockedPaneladd = false;
    }
