    // 
    getCustomerAge(customerBirthDate: string): Number {
        const birthDataArray = customerBirthDate.split('-');
        const birthYear = birthDataArray[0];
        const birthMonth = birthDataArray[1];
        const birthDay = birthDataArray[2];

        const d = new Date();
        const nowYear = d.getFullYear();
        const nowMonth = d.getMonth() + 1;
        const nowDay = d.getDay();

        let returnAge: Number;

        if (nowYear == Number(birthYear)) {
            returnAge = 0;
        } else {
            let ageDiff = nowYear - Number(birthYear);
            if (ageDiff > 0) {
                if (nowMonth == Number(birthMonth)) {
                    let dayDiff = nowDay - Number(birthDay);
                    if (dayDiff < 0) {
                        returnAge = ageDiff - 1;
                    } else {
                        returnAge = ageDiff;
                    }
                } else {
                    let monthDiff = nowMonth - Number(birthMonth);
                    if (monthDiff < 0) {
                        returnAge = ageDiff - 1;
                    } else {
                        returnAge = ageDiff;
                    }
                }
            } else {
                returnAge = -1;
            }
        }
        return returnAge;
    }

