    querylists(){
      this.customerListBean['pageSize'] = this.pageSizes;
      this.customerListBean['pageNum'] = this.pageNum;
      this.customerListBean['dataSourceId'] = this.dataSourceId;
      // this.doClearCheck()
      let ob = this.creditCcmService.myCustDimQry(this.customerListBean);
      ob.subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.dataTable = data.infoList;
          this.totalNum = data.totalNum;
      }
        else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
        }
      }, error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: '' });
      })
    }
    paginate(event) {
        //
        this.pageSizes = event.rows * 1;
        //
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        this.first = event.page * event.rows;
        //
        this.queryList();
    }

    //
    doChangeAmt(mod, i){
        if(mod && mod=='add'){
            if(i && i == 'pledgeAmt'){
                if(this.mortgageBean.pledgeAmt && this.mortgageBean.pledgeAmt.toString().indexOf(",")==-1){
                    this.mortgageBean.pledgeAmt = this.commfunc.formatMoney(this.mortgageBean.pledgeAmt,2);
                }
            } else if(i && i == 'guaranteeContractAmt'){
                if(this.mortgageBean.contractAmt && this.mortgageBean.contractAmt.toString().indexOf(",")==-1){
                    this.mortgageBean.contractAmt = this.commfunc.formatMoney(this.mortgageBean.contractAmt,2);
                }
            }            
        } else {
            if(i && i == 'pledgeAmt'){
                if(this.mortgageBean.pledgeAmt && this.mortgageBean.pledgeAmt.toString().indexOf(",")!=-1){//
                    this.mortgageBean.pledgeAmt = this.mortgageBean.pledgeAmt.replace(/\,/g, '');
                }
            } else if(i && i == 'guaranteeContractAmt'){
                if(this.mortgageBean.contractAmt && this.mortgageBean.contractAmt.toString().indexOf(",")!=-1){//
                    this.mortgageBean.contractAmt = this.mortgageBean.contractAmt.replace(/\,/g, '');
                }
            }                   
        }
    }

