    /**
     * 
     */
    pledgeAmtChange(mod) {
        if(mod && mod == '1'){
            if (this.pledgeBean.pledgeAmt && this.pledgeBean.pledgeContractBalance) {
                let pledgeAmt1 = this.pledgeBean.pledgeAmt;
                if(pledgeAmt1 && pledgeAmt1.indexOf(",")!=-1){
                    pledgeAmt1 = this.pledgeBean.pledgeAmt.replace(/\,/g, '');
                }
                let pledgeContractBalance1 = this.pledgeBean.pledgeContractBalance;
                if(pledgeContractBalance1 && pledgeContractBalance1.indexOf(",")!=-1){
                    pledgeContractBalance1 = this.pledgeBean.pledgeContractBalance.replace(/\,/g, '');
                }
                if (Number(pledgeAmt1) > Number(pledgeContractBalance1)) {
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    this.pledgeBean.pledgeAmt = '';
                    return;
                } else if (Number(pledgeAmt1) < 0) {
                    this.msgs.push({ severity: 'error', summary: '', detail: '0' });
                    this.pledgeBean.pledgeAmt = '';
                    return;
                }
            }           
        }
    }

